body {
  font: 14px "Verdana", Futura, sans-serif;
  background-color: rgb(73, 105, 112);
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: rgb(255, 246, 209);
  border: 1px solid rgb(6, 5, 5);
  float: left;
  font-size: 35px;
  font-weight: bold;
  line-height: 34px;
  height: 75px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 75px;
}

.square-winner {
  background: rgb(255, 246, 209);
  border: 1px solid rgb(6, 5, 5);
  float: left;
  color:rgb(255, 246, 209);
  font-size: 45px;
  font-weight: bold;
  line-height: 34px;
  height: 75px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 75px;
  text-shadow: 1px 1px 2px black, 0 0 25px rgb(251, 236, 77), 0 0 5px rgb(100, 94, 5);
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  background-color: rgb(183, 231, 221);
  margin:auto;
}

.game-info {
  background-color: rgba(133, 133, 133, 0.3);
}

.g-in {
  color:rgb(255, 255, 255);
}

.header {
  background: rgb(183, 231, 221);
}

.heading {
  color:rgb(73, 105, 112);
  margin:20px 0px 15px 0px;
  text-shadow: 3px 3px 8px rgb(216, 146, 146);
}

.play {
  font-size: 30px;
  padding:20px;
  margin:70px;
  border:none;
  border-radius: 80px;
  text-align: center;
  background-color: cadetblue;
}

.play:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

footer {
  background-color: #000000;
  color:aliceblue;
}

i {
  font-size: 48px;
  margin: 10px;
}


